<template>
  <div class="aside">
    <h3 class="main__name">
      <a
        :href="$route.params.itemRef ? $route.params.itemRef : foundItem.ref"
        target="_blank"
        >{{
          $route.params.itemName ? $route.params.itemName : foundItem.name
        }}</a
      >
    </h3>
    <p class="descr">
      {{
        $route.params.itemDescription
          ? $route.params.itemDescription
          : foundItem.description
      }}
    </p>
    <img
      class="img__species"
      :src="$route.params.itemImg ? $route.params.itemImg : foundItem.img"
      alt=""
    />

    <router-link
      class="back"
      :to="`${
        $route.params.itemPrevious
          ? $route.params.itemPrevious
          : foundItem.previous
      }`"
      >Обратно</router-link
    >
  </div>
</template>

<script>
export default {
  name: "SpeciesPage",

  data() {
    return {
      publicPath: process.env.BASE_URL,
      dataSpecies: this.$route.params,
      itemRout: this.$route.params.itemRout,
      foundItem: null,
    };
  },
  beforeMount() {
    this.getFromLocalStorage();
    this.paramsHoisting();
  },
  methods: {
    paramsHoisting() {
      this.$emit("clicked", this.foundItem);
    },
    getFromLocalStorage() {
      let storage = JSON.parse(localStorage.data);
      let previous = localStorage.previous;
      this.foundItem = storage.find((item) => item.routing == this.itemRout);
      this.foundItem.previous = previous;
      return this.foundItem;
    },
  },
  emits: {
    clicked: null,
    destroy: null,
  },
  created() {},
  beforeUnmount() {
    this.$emit("destroy");
  },
};
</script>

<style>
@font-face {
  font-family: "Old";
  src: url("../../assets/old.ttf") format("truetype");
}
.aside {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  perspective: 2000px;
}
.img__species {
  border: 3px solid black;
  max-width: 350px;
  max-height: 350px;
  transition: transform 0.5s linear, box-shadow 0.5s linear;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-name: species-img;
}
@keyframes species-img {
  0% {
    transform: rotateY(25deg);
    box-shadow: -18px 4px 4px 0px rgba(34, 60, 80, 0.3);
  }
  50% {
    box-shadow: 20px 4px 4px 0px rgba(34, 60, 80, 0.3);

    transform: rotateY(-25deg);
  }
  100% {
    transform: rotateY(25deg);
    box-shadow: -18px 4px 4px 0px rgba(34, 60, 80, 0.3);
  }
}
.main__name {
  text-transform: uppercase;
  font-weight: 700;
  margin: 0;
  text-align: center;
  text-decoration: underline 3px;
  margin-bottom: 10px;
  font-size: 20px;
}
.main__name > a {
  text-decoration: none;
  color: #4682b4;
}
.descr {
  margin-top: 0;
  text-align: center;
  max-width: 300px;
  font-size: 18px;
  font-family: "Old";
}
@media (max-width: 1024px) {
  .main__title {
    margin-block-end: 0;
  }
  .aside {
    position: relative;
  }
  .img__species {
    -webkit-box-shadow: 0px 0px 9px 5px rgba(44, 156, 167, 0.4);
    -moz-box-shadow: 0px 0px 9px 5px rgba(44, 156, 167, 0.4);
    box-shadow: 0px 0px 9px 5px rgba(44, 156, 167, 0.4);
    max-width: 250px;
    max-height: 250px;
  }
  .descr {
    padding: 20px;
    font-size: 14px;
  }
  .back {
    margin-top: 10px;
  }
}
@media (max-width: 768px) {
  .main__title {
    margin-block-end: 20px;
  }
  .descr {
    font-size: 18px;
  }
  .img__species {
    max-width: 350px;
    max-height: 350px;
  }
}
@media (max-width: 340px) {
  .main__title {
    margin-block-end: 20px;
  }
  .descr {
    font-size: 15px;
  }
  .img__species {
    max-width: 350px;
    max-height: 350px;
  }
}
</style>
